.parent {
    /* width: 900px; */
    margin: auto;
    padding: 0.5rem;
    background: #ffffff;
    border-radius: 25px;
    /* box-shadow: 7px 20px 20px rgb(210, 227, 244); */
}

.file-upload {
    text-align: center;
    border: 3px dashed rgb(210, 227, 244);
    padding: 0.5rem;
    position: relative;
    cursor: pointer;
}

.file-upload p {
    font-size: 0.87rem;
    margin-top: 10px;
    color: #bbcada;
}

.file-upload input {
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    cursor: pointer;
}